/* apply a natural box layout model to all elements, but allowing components to change */
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }

.container {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto; }

img {
  width: 100%; }

a {
  text-decoration: none; }

h1, h2, h3, h4, h5 {
  margin: 0;
  font-family: "Playfair Display", serif; }

h1 {
  font-size: 5rem; }

h2 {
  font-size: 4.5rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.5rem; }

h5 {
  font-size: 2rem; }

.centrar-texto {
  text-align: center; }

.subtitle {
  padding: 3rem 0; }

.italic {
  font-style: italic; }

.bold {
  font-weight: 700; }

.light {
  font-weight: 300; }

.gray1 {
  color: #BDBDBD; }

.gray2 {
  color: #4F4F4F; }

.campo {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.66667rem; }
  .campo [type="checkbox"] {
    width: 2rem;
    height: 2rem;
    cursor: pointer; }

.step {
  display: flex;
  gap: 1rem;
  padding-top: 2rem; }

.number {
  font-family: "Playfair Display", serif;
  color: #fff;
  background-color: #F2994A;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4rem;
  height: 4rem;
  border-radius: .6rem; }

@media (min-width: 768px) {
  .yields-ingredients {
    display: grid;
    justify-content: space-between;
    grid-template-columns: 2fr 1fr; }
  .ingredients {
    grid-row: 1 / 2;
    grid-column: 1 / 2; }
  .yields {
    grid-column: 2 / 3;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    -webkit-box-shadow: 4px 4px 9px 0px rgba(50, 50, 50, 0.75);
    -moz-box-shadow: 4px 4px 9px 0px rgba(50, 50, 50, 0.75);
    box-shadow: 4px 4px 9px 0px rgba(50, 50, 50, 0.75);
    width: 20rem;
    height: 30rem;
    border-radius: 1rem; } }

/*==================================== Layout ====================================*/
.resumen, .resumen-text {
  padding: 2.5rem 0; }

.resumen-text {
  display: flex;
  gap: 1rem; }
  .resumen-text p {
    margin: 0; }

.yields-serving {
  display: flex;
  align-items: center;
  gap: 1rem; }
  .yields-serving p {
    margin: 0;
    color: #BDBDBD; }
  .yields-serving span {
    color: #F2994A; }

.yields-time {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  padding: 2.5rem 0; }

.prep-time, .cook-time, .total-time {
  display: flex;
  align-items: center; }
  .prep-time p, .cook-time p, .total-time p {
    margin: 0;
    color: #BDBDBD;
    text-transform: uppercase;
    font-size: 1.1rem; }
  .prep-time span, .cook-time span, .total-time span {
    color: #000;
    text-transform: initial;
    font-size: 1.3rem; }
  @media (min-width: 768px) {
    .prep-time p, .cook-time p, .total-time p {
      font-size: 1.3rem; }
    .prep-time span, .cook-time span, .total-time span {
      font-size: 1.4rem; } }

@media (min-width: 768px) {
  .yields-serving, .yields-time {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; } }

.instructions {
  padding: 3rem 0; }
